@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,900&display=swap');

.comments {
  margin-top: 20px;
  color: #ffffff;
}

body {
  background-color: #0e0e10;
  max-width: 1600px;
  
}
.comments-title {
  font-size: 30px;
  margin-bottom: 20px;
  color: #ffffff;
}

.heading {
  color: white;
}


.comments-container {
  margin-top: 40px;
}

.comment-form-title {
  position: relative;
  top: 0rem;
  font-size: 22px;
  color: #ffffff;
}

.name-form-title {
  position: relative;
  top: -1.64rem;
  left: 15rem;
  font-size: 22px;
  color: #ffffff;
}

.comment-form-textarea {
  position: relative;
  top: -50rem;
  left: 11.2rem;
  font-size: 1rem;
  width: 20%;
  height: 10rem;
  margin-bottom: 8px;
  margin-top: 8px;
  border: 1px solid rgb(107, 114, 12);
  color: black;
}

.name-form-textarea {
  position: relative;
  top: -9rem;
  width: 10%;
  height: 30px;
  margin-bottom: 8px;
  margin-top: 8px;
  border: 1px solid rgb(107, 114, 12);
  color: black;
}

.comment-form-button {
  position: relative;
  top: -51.7rem;
  left: 6%;
  font-size: 16px;
  padding: 8px 16px;
  background: rgb(59, 130, 246);
  border-radius: 8px;
  color: white;
}

.comment-form-button:hover:enabled {
  cursor: pointer;
  background: rgb(37, 99, 235);
}

.comment-form-button:disabled {
  opacity: 0.7;
  cursor: default;
}

.comment-form-cancel-button {
  margin-left: 10px;
}

.comment {
  display: flex;
  margin-bottom: 28px;
  color: #ffffff;
}

.comment-image-container {
  margin-right: 12px;
}

.comment-image-container img {
  border-radius: 50px;
}

.comment-right-part {
  width: 100%;
}

.comment-content {
  display: flex;
  position: relative;
  top: -38rem;
  left: 57.2%;
}

.comment-author {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
}

.comment-text {
  position: relative;
  top: -38rem;
  left: 8.7rem;
  font-size: 18px;
}

.name-text {
  margin-right: 8px;
  font-size: 20px;
  color: rgb(59, 130, 246);
}

.comment-actions {
  position: relative;
  top: -38rem;
  left: 56.7%;
  display: flex;
  font-size: 12px;
  color: #ffffff;
  cursor: pointer;
  margin-top: 8px;
}

.comment-action {
  margin-right: 8px;
}

.comment-action:hover {
  text-decoration: underline;
}

.replies {
  position: relative;
  left: 3%;
  color: blue;
  top: -1.9rem;
  margin-top: 20px;
}
